/* Hero Section */
.home {
  max-width: 1000px;
  margin: 0 auto;
  padding: var(--space-2xl);
  text-align: center;
  background: rgba(255, 255, 255, 0.95);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-2xl);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
  overflow: hidden;
}

.home::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent 30%,
    rgba(0, 150, 57, 0.03) 50%,
    transparent 70%
  );
  animation: rotate 20s linear infinite;
  pointer-events: none;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Title Styling */
.home h2 {
  font-size: 3.5rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--primary-green) 0%, var(--primary-green-dark) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: var(--space-lg);
  position: relative;
  z-index: 2;
}

.home h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, var(--secondary-yellow), var(--accent-orange));
  border-radius: var(--radius-full);
}

/* Description */
.home p {
  font-size: 1.3rem;
  color: var(--gray-700);
  margin-bottom: var(--space-2xl);
  line-height: 1.8;
  position: relative;
  z-index: 2;
}

/* Hero Image Container */
.hero-image-container {
  position: relative;
  margin: var(--space-2xl) 0;
  display: inline-block;
}

.home img {
  max-width: 100%;
  height: 450px;
  object-fit: cover;
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-xl);
  transition: all var(--transition-slow);
  position: relative;
  z-index: 2;
}

.home img:hover {
  transform: scale(1.02) translateY(-5px);
  box-shadow: var(--shadow-2xl);
}

/* Floating Elements */
.hero-image-container::before,
.hero-image-container::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: var(--secondary-yellow);
  border-radius: 50%;
  animation: float 3s ease-in-out infinite;
}

.hero-image-container::before {
  top: -10px;
  left: -10px;
  animation-delay: 0s;
}

.hero-image-container::after {
  bottom: -10px;
  right: -10px;
  animation-delay: 1.5s;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

/* Promo Section */
.promo-section {
  background: linear-gradient(135deg, #fff9e6 0%, #fff3cd 100%);
  border: 2px solid var(--secondary-yellow);
  border-radius: var(--radius-xl);
  padding: var(--space-xl);
  margin: var(--space-2xl) 0;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.promo-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 215, 0, 0.1), transparent);
  animation: shimmer 3s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.promo-section h3 {
  color: var(--accent-orange);
  margin-bottom: var(--space-lg);
  font-size: 1.8rem;
  font-weight: 600;
  position: relative;
  z-index: 2;
}

.promo-section h3::before {
  content: '🎉';
  margin-right: var(--space-sm);
  font-size: 1.5rem;
}

/* Promo Codes */
.promo-codes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-md);
  margin-top: var(--space-lg);
}

.promo-code {
  background: linear-gradient(135deg, var(--white) 0%, #fff8e1 100%);
  border: 2px solid var(--secondary-yellow);
  padding: var(--space-lg);
  border-radius: var(--radius-lg);
  font-weight: 600;
  color: var(--gray-800);
  transition: all var(--transition-normal);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.promo-code::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent, rgba(255, 215, 0, 0.1), transparent);
  transform: translateX(-100%);
  transition: transform var(--transition-slow);
}

.promo-code:hover::before {
  transform: translateX(100%);
}

.promo-code:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-orange);
}

.promo-code strong {
  color: var(--primary-green);
  font-size: 1.1em;
}

/* Features Section */
.features-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-lg);
  margin: var(--space-2xl) 0;
}

.feature-card {
  background: var(--white);
  padding: var(--space-lg);
  border-radius: var(--radius-lg);
  text-align: center;
  box-shadow: var(--shadow-md);
  transition: all var(--transition-normal);
  border: 1px solid var(--gray-200);
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-xl);
  border-color: var(--primary-green);
}

.feature-icon {
  font-size: 2.5rem;
  margin-bottom: var(--space-md);
  display: block;
}

.feature-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--gray-900);
  margin-bottom: var(--space-sm);
}

.feature-description {
  font-size: 0.9rem;
  color: var(--gray-600);
  line-height: 1.6;
}

/* CTA Section */
.cta-section {
  background: linear-gradient(135deg, var(--primary-green) 0%, var(--primary-green-dark) 100%);
  color: var(--white);
  padding: var(--space-2xl);
  border-radius: var(--radius-xl);
  margin: var(--space-2xl) 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.cta-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  opacity: 0.3;
}

.cta-section h3 {
  font-size: 2rem;
  margin-bottom: var(--space-md);
  position: relative;
  z-index: 2;
}

.cta-section p {
  font-size: 1.1rem;
  margin-bottom: var(--space-lg);
  opacity: 0.9;
  position: relative;
  z-index: 2;
}

.cta-button {
  display: inline-block;
  background: var(--secondary-yellow);
  color: var(--gray-900);
  padding: var(--space-md) var(--space-xl);
  border-radius: var(--radius-full);
  font-weight: 600;
  font-size: 1.1rem;
  text-decoration: none;
  transition: all var(--transition-normal);
  border: none;
  cursor: pointer;
  position: relative;
  z-index: 2;
}

.cta-button:hover {
  background: var(--secondary-yellow-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* Responsive Design */
@media (max-width: 768px) {
  .home {
    margin: var(--space-md);
    padding: var(--space-lg);
  }
  
  .home h2 {
    font-size: 2.5rem;
  }
  
  .home p {
    font-size: 1.1rem;
  }
  
  .home img {
    height: 300px;
  }
  
  .promo-codes-grid {
    grid-template-columns: 1fr;
  }
  
  .features-section {
    grid-template-columns: 1fr;
  }
  
  .cta-section h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .home h2 {
    font-size: 2rem;
  }
  
  .home p {
    font-size: 1rem;
  }
  
  .home img {
    height: 250px;
  }
  
  .promo-section {
    padding: var(--space-lg);
  }
  
  .cta-section {
    padding: var(--space-lg);
  }
}

/* Animation Classes */
.fade-in-up {
  animation: fadeInUp 0.8s ease-out;
}

.slide-in-left {
  animation: slideInLeft 0.8s ease-out;
}

.scale-in {
  animation: scaleIn 0.6s ease-out;
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Loading States */
.loading {
  opacity: 0.7;
  pointer-events: none;
}

.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid var(--primary-green);
  border-top-color: transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}
