);\n  position: relative;\n  z-index: 2;\n}\n\n.contact h2::after {\n  content: \"\";\n  position: absolute;\n  bottom: -10px;\n  left: 50%;\n  transform: translateX(-50%);\n  width: 80px;\n  height: 4px;\n  background: linear-gradient(\n    90deg,\n    var(--secondary-yellow),\n    var(--accent-orange)\n  );\n  border-radius: var(--radius-full);\n}\n\n/* Contact Subtitle */\n.contact-subtitle {\n  text-align: center;\n  font-size: 1.2rem;\n  color: var(--gray-600);\n  margin-bottom: var(--space-2xl);\n  max-width: 600px;\n  margin-left: auto;\n  margin-right: auto;\n  line-height: 1.6;\n  position: relative;\n  z-index: 2;\n}\n\n/* Contact Content Layout */\n.contact-content {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  gap: var(--space-2xl);\n  align-items: start;\n  position: relative;\n  z-index: 2;\n}\n\n/* Form Section */\n.form {\n  background: var(--white);\n  padding: var(--space-xl);\n  border-radius: var(--radius-xl);\n  box-shadow: var(--shadow-lg);\n  border: 1px solid var(--gray-200);\n  transition: all var(--transition-normal);\n}\n\n.form:hover {\n  box-shadow: var(--shadow-xl);\n  border-color: var(--primary-green);\n}\n\n/* Form Groups */\n.form div {\n  display: flex;\n  flex-direction: column;\n  margin-bottom: var(--space-lg);\n  position: relative;\n}\n\n.form div:last-child {\n  margin-bottom: 0;\n}\n\n/* Labels */\nlabel {\n  margin-bottom: var(--space-sm);\n  font-weight: 600;\n  color: var(--gray-700);\n  font-size: 0.95rem;\n  transition: color var(--transition-fast);\n}\n\n.form div:focus-within label {\n  color: var(--primary-green);\n}\n\n/* Input Fields */\ninput,\ntextarea {\n  padding: var(--space-md);\n  border: 2px solid var(--gray-200);\n  border-radius: var(--radius-lg);\n  font-size: 1rem;\n  font-family: var(--font-primary);\n  transition: all var(--transition-normal);\n  background: var(--white);\n  position: relative;\n}\n\ninput:focus,\ntextarea:focus {\n  outline: none;\n  border-color: var(--primary-green);\n  box-shadow: 0 0 0 3px rgba(0, 150, 57, 0.1);\n  transform: translateY(-2px);\n}\n\n/* Input Icons - Fixed positioning */\n.form div {\n  position: relative;\n}\n\n/* Remove all pseudo-element icons to prevent duplication */\n.form div::before,\n.form div::after {\n  display: none !important;\n}\n\n/* Adjust input padding for icons */\n.form div:nth-child(-n + 3) input {\n  padding-left: var(--space-md);\n}\n\n.form div:nth-child(4) textarea {\n  padding-left: var(--space-md);\n}\n\n/* Textarea specific styles */\ntextarea {\n  min-height: 120px;\n  resize: vertical;\n  line-height: 1.6;\n}\n\n/* Submit Button */\n.submit-btn {\n  background: linear-gradient(\n    135deg,\n    var(--primary-green) 0%,\n    var(--primary-green-dark) 100%\n  );\n  color: var(--white);\n  padding: var(--space-md) var(--space-xl);\n  border: none;\n  border-radius: var(--radius-full);\n  cursor: pointer;\n  font-weight: 600;\n  font-size: 1.1rem;\n  transition: all var(--transition-normal);\n  position: relative;\n  overflow: hidden;\n  width: 100%;\n  margin-top: var(--space-md);\n}\n\n.submit-btn::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: -100%;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(\n    90deg,\n    transparent,\n    rgba(255, 255, 255, 0.2),\n    transparent\n  );\n  transition: left var(--transition-slow);\n}\n\n.submit-btn:hover::before {\n  left: 100%;\n}\n\n.submit-btn:hover {\n  transform: translateY(-3px);\n  box-shadow: var(--shadow-xl);\n}\n\n.submit-btn:active {\n  transform: translateY(-1px);\n}\n\n/* Contact Info Section */\n.contact-info {\n  background: linear-gradient(135deg, var(--gray-50) 0%, var(--white) 100%);\n  padding: var(--space-xl);\n  border-radius: var(--radius-xl);\n  border: 1px solid var(--gray-200);\n  height: fit-content;\n}\n\n.contact-info h3 {\n  color: var(--gray-900);\n  font-size: 1.5rem;\n  margin-bottom: var(--space-lg);\n  font-weight: 600;\n}\n\n.contact-method {\n  display: flex;\n  align-items: center;\n  margin-bottom: var(--space-lg);\n  padding: var(--space-md);\n  background: var(--white);\n  border-radius: var(--radius-lg);\n  border: 1px solid var(--gray-200);\n  transition: all var(--transition-normal);\n}\n\n.contact-method:hover {\n  border-color: var(--primary-green);\n  transform: translateX(5px);\n  box-shadow: var(--shadow-md);\n}\n\n.contact-icon {\n  font-size: 1.5rem;\n  margin-right: var(--space-md);\n  width: 40px;\n  height: 40px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background: var(--primary-green);\n  color: var(--white);\n  border-radius: 50%;\n}\n\n.contact-details h4 {\n  color: var(--gray-900);\n  font-size: 1rem;\n  margin-bottom: var(--space-xs);\n  font-weight: 600;\n}\n\n.contact-details p {\n  color: var(--gray-600);\n  font-size: 0.9rem;\n  margin: 0;\n}\n\n/* Side Image - Fixed sizing */\n.contactSideImg {\n  width: 100%;\n  height: 400px;\n  object-fit: cover;\n  border-radius: var(--radius-xl);\n  box-shadow: var(--shadow-lg);\n  transition: all var(--transition-slow);\n  margin-top: var(--space-lg);\n}\n\n.contactSideImg:hover {\n  transform: scale(1.02);\n  box-shadow: var(--shadow-xl);\n}\n\n/* Success Message */\n.success-message {\n  background: linear-gradient(135deg, #4caf50, #45a049);\n  color: var(--white);\n  padding: var(--space-lg);\n  border-radius: var(--radius-lg);\n  text-align: center;\n  margin-top: var(--space-lg);\n  animation: slideInUp 0.5s ease-out;\n}\n\n.success-message h3 {\n  color: var(--white);\n  margin-bottom: var(--space-sm);\n}\n\n/* Loading State */\n.form.loading {\n  opacity: 0.7;\n  pointer-events: none;\n}\n\n.form.loading::after {\n  content: \"\";\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  width: 30px;\n  height: 30px;\n  margin: -15px 0 0 -15px;\n  border: 3px solid var(--gray-200);\n  border-top-color: var(--primary-green);\n  border-radius: 50%;\n  animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* Responsive Design */\n@media (max-width: 768px) {\n  .contact {\n    margin: var(--space-md);\n    padding: var(--space-lg);\n  }\n\n  .contact h2 {\n    font-size: 2.5rem;\n  }\n\n  .contact-subtitle {\n    font-size: 1.1rem;\n  }\n\n  .contact-content {\n    grid-template-columns: 1fr;\n    gap: var(--space-lg);\n  }\n\n  .form {\n    padding: var(--space-lg);\n  }\n\n  .contact-info {\n    padding: var(--space-lg);\n  }\n\n  .contactSideImg {\n    height: 300px;\n  }\n}\n\n@media (max-width: 480px) {\n  .contact h2 {\n    font-size: 2rem;\n  }\n\n  .contact-subtitle {\n    font-size: 1rem;\n  }\n\n  .contact {\n    padding: var(--space-md);\n  }\n\n  .form {\n    padding: var(--space-md);\n  }\n\n  .contact-info {\n    padding: var(--space-md);\n  }\n\n  .contactSideImg {\n    height: 250px;\n  }\n\n  .submit-btn {\n    padding: var(--space-md);\n    font-size: 1rem;\n  }\n}\n\n/* Animation Classes */\n.contact {\n  animation: fadeInUp 0.8s ease-out;\n}\n\n.form {\n  animation: slideInLeft 0.8s ease-out;\n}\n\n.contact-info {\n  animation: slideInRight 0.8s ease-out;\n}\n\n@keyframes slideInRight {\n  from {\n    opacity: 0;\n    transform: translateX(30px);\n  }\n  to {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n/* Form Validation Styles */\n.form div.error input,\n.form div.error textarea {\n  border-color: var(--accent-red);\n  box-shadow: 0 0 0 3px rgba(231, 76, 60, 0.1);\n}\n\n.form div.error label {\n  color: var(--accent-red);\n}\n\n.error-message {\n  color: var(--accent-red);\n  font-size: 0.85rem;\n  margin-top: var(--space-xs);\n  display: none;\n}\n\n.form div.error .error-message {\n  display: block;\n}\n\n/* Focus States for Accessibility */\ninput:focus,\ntextarea:focus,\n.submit-btn:focus {\n  outline: 3px solid var(--primary-green);\n  outline-offset: 2px;\n}\n\n/* Hover Effects */\n.contact-method:hover .contact-icon {\n  transform: scale(1.1);\n}\n\n/* Custom Scrollbar for Textarea */\ntextarea::-webkit-scrollbar {\n  width: 6px;\n}\n\ntextarea::-webkit-scrollbar-track {\n  background: var(--gray-100);\n  border-radius: var(--radius-sm);\n}\n\ntextarea::-webkit-scrollbar-thumb {\n  background: var(--gray-400);\n  border-radius: var(--radius-sm);\n}\n\ntextarea::-webkit-scrollbar-thumb:hover {\n  background: var(--gray-500);\n}\n"],"sourceRoot":""}]);
